button {
  margin: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
  background-color: #FF4500;
  color: white;
  border: none;
  border-radius: 45px;
  transition: background-color 0.3s ease;
  font-family: 'Outfit', sans-serif;
}

button:hover {
  background-color: #FF0000;
}

/* Messages Container */
#messages {
  height: 50vh;
  overflow-y: auto;
  margin-top: 20px;
  padding: 10px;
  border-radius: 12px;
  background-color: #fff;
  display: flex;
  flex-direction: column;  /* NORMAL flow, but we fix positioning */
  justify-content: flex-end;  /* NEW: Keeps messages at the bottom */
}

/* Individual Messages */
.message-div {
  max-width: 70%;
  padding: 12px 16px;
  border-radius: 12px;
  font-size: 16px;
  word-wrap: break-word;
  margin: 4px 0;
  display: inline-block;
  transition: background 0.5s ease;
}

.alert-div {
  width: 100%;
  padding: 12px 0px;
  border-radius: 12px;
  font-size: 16px;
  word-wrap: break-word;
  margin: 8px 0;
  display: inline-block;
  background-color:#888 ;
  color: #fff;
}


.my-message {
  color:#FF4500 ;
  background-color: #FF450020;
  align-self: flex-end; /* Ensure my messages stay on the right */
  text-align: right;
  border-bottom-right-radius: 0px;
}


.peer-message {
  color:#333 ;
  background-color: #e9e9e9;
  align-self: flex-start; /* Ensure peer messages stay on the left */
  text-align: left;
  border-bottom-left-radius: 0px;
}

/* Scrollbar customization */
#messages::-webkit-scrollbar {
  width: 6px;
}

#messages::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

#messages::-webkit-scrollbar-track {
  background-color: #f0f2f5;
}
.emoji{
  font-size: 32px;
  margin: 0px 10px;
}
#imageUploadBtn{
  font-size: 30px;
}
    .emoji-div {
      display: inline-block;
      font-size: 32px;
      margin: 4px 0px;
      padding: 8px 10px;
      border-radius: 12px;
      background-color: rgba(255, 255, 255, 0.8);
      position: relative;
      animation: fadeOut 1s forwards;
    }
    .emoji-right {
      align-self: flex-end;
      background-color: #333;
      border-bottom-right-radius: 0px;
      color: white;
    }
    .emoji-left {
      align-self: flex-start;
      background-color: #333;
      border-bottom-left-radius: 0px;
      color: white;
    }
       .image-div {
      display: block;
        margin:0px;
      padding: 0px;
      border-radius: 10px;
      background-color: rgba(255, 255, 255, 0.8);
    }
    .image-right {
      align-self: flex-end;
       border-bottom-right-radius: 0px;
    }
    .image-left {
      align-self: flex-start;
      border-bottom-left-radius: 0px;
    }
    .image-div img {
      max-width: 200px;
      max-height: 200px;
      border-radius: 12px;
    }

    .offscreen {
  position: absolute;
  left: -9999px;
  top: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
        /* Boom shadow effect */
        .boom-shadow {
            box-shadow: 0 0 5px 5px rgba(0, 0, 0, 1);
        }



/* Typing animation gradient */
.typing-gradient {
  background: linear-gradient(90deg, rgba(255,69,0,0.1), rgba(0,186,255,0.3), rgba(255,69,0,0.1));
  background-size: 200% 100%;
  animation: typingGradientAnimation 2s linear infinite;
  color:  rgba(255,69,0,1);
}


/* Keyframe for shimmer */
@keyframes typingGradientAnimation {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}




