body {
  font-family: 'Outfit', sans-serif;
}

.avatar-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f0f0f0;
  padding: 20px;
}

h1 {
  font-size: 24px;
  color: #333;
}

h2 {
  font-size: 18px;
  color: #555;
  margin-top: 20px;
}

#avatarGallery {
  margin-top: 30px;
}

.avatar-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  margin: 5%;
  place-items: center;
  width: 90%;

}

.avatar {
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 50px;
}

.avatar:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.upload-wrapper {
  margin-top: 20px;
}

input[type="file"] {
  display: none;
}

.upload-button,
.skip-button {
  width: 100%;
  margin: 10px 0px;
  font-size: 16px;
  cursor: pointer;
}

.upload-button{
  color: #fff;
}
.skip-button{
  color: #999;
}

.upload-button:hover{
  background-color: #FF0000;
}

input[type="file"] + label {
  display: inline-block;
  padding: 10px 0px;
  background-color: #f0f0f0;
  border-radius: 50px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 15px;
  color: #444;
}

input[type="file"]:focus + label,
input[type="file"]:hover + label {
  background-color: #ddd;
}

